section.blog {
  height: auto;
  .blog-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    margin: 0 auto;
    text-align: center;
    padding-top: 50px;
    h3 {
      font-size: 3.0rem;
      color: $primary; }
    .filter-bar {
      width: 100%;
      display: flex;
      margin: 0 auto;
      justify-content: center;
      margin-bottom: 30px;
      .input-container {
        width: 38%;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 15px 0 0 15px;
        select#filter {
          width: 70%;
          height: 100%;
          border: none;
          background-color: $input-bg;
          padding-left: 15px;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          border-radius: inherit;
          &:focus {
            outline: none; } }
        a.search-btn {
          width: 25%;
          height: 100%;
          background-color: $primary;
          color: #fff;
          font-size: 1rem;
          border-radius: 0 15px 15px 0; }
        input#search {
          width: 70%;
          height: 100%;
          padding: 0;
          border: none;
          background-color: $input-bg;
          padding-left: 15px;
          border-radius: inherit;
          &:focus {
            outline: none; } } } }
    .post-wrapper {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(330px, 29%));
      grid-gap: 30px 0;
      margin: 30px 0 50px;
      justify-content: center;
      .post {
        height: 485px;
        width: 330px;
        @include card-shadow;
        background-color: #fff;
        border-radius: 0 0 15px 15px;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        .image-container {
          height: 40%;
          border-radius: 15px 15px 0 0;
          img {
            height: 100%;
            width: 100%;
            border-radius: inherit; } }
        .text-container {
          display: flex;
          flex-direction: column;
          height: 55%;
          text-align: left;
          padding: 15px;
          justify-content: space-around;
          border-radius: 0 0 15px 15px;
          background-color: #fbfbfb;
          .line-one {
            display: flex;
            justify-content: space-between;
            span {
              font-size: .9rem;
              color: $sub-text-light; }
            span#cat::before {
              content: "\2022";
              color: $sub-text;
              margin-right: 5px;
              font-weight: 800; } }
          .article-content {
            height: 65%;
            h4 {
              font-size: 1.2rem;
              margin: 0 0 10px;
              color: $sub-text;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              overflow: hidden; }
            p {
              font-size: 1.0rem;
              margin: 10px 0;
              color: $sub-text;
              display: -webkit-box;
              -webkit-line-clamp: 5;
              -webkit-box-orient: vertical;
              overflow: hidden; } }
          a#more {
            font-size: .9rem;
            margin-left: auto;
            &::after {
              content: "\27f6";
              color: $primary;
              margin-left: 5px; } } } } } } }
